@import url(https://fonts.googleapis.com/css?family=Noto+Sans+TC:100,300,400,500,700,900&display=swap);
.header {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	width: 100%;
	height: 10vh;
	-webkit-align-items: center;
	        align-items: center;
	padding-right: 2%;
	position: fixed;
	top: 0;
	background-color: #fff;
	z-index: 50;
}

.header-name {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	margin-left: 5%;
}

.name {
	font: normal normal bold 25px/1.4em poppins-semibold, poppins, sans-serif;
}

.office {
	font: normal normal normal 17px/1.4em avenir-lt, sans-serif;
}

.header-name a {
	color: #000000;
	text-decoration: none;
}

.header-name span {
	margin: 0 5px;
}

.nav {
	display: -webkit-flex;
	display: flex;
}

.mobile-nav-container {
	display: none;
}

.nav a {
	margin-right: 12px;
	color: #000000;
	text-decoration: none;
	font: normal normal normal 14px/1.4em 'avenir-lt', sans-serif;
}

.nav a.active {
	color: #0050ff;
}

.nav a:hover {
	color: #0050ff;
}

.mobile-nav {
	display: none;
}

@media (max-width: 575.99px) {
	.header {
		height: 20vh;
	}

	.header-name {
		-webkit-flex-direction: column;
		        flex-direction: column;
		-webkit-align-items: flex-start;
		        align-items: flex-start;
	}

	.header-name span {
		display: none;
	}

	.header-name a {
		display: block;
		font-size: 30px;
	}

	.nav {
		display: none;
	}

	.mobile-nav-container {
		display: block;
		width: 100%;
		height: 100%;
		background-color: #f4ece6;
		position: fixed;
		top: -100%;
		z-index: 10;
		transition: all 0.5s ease-in;
		opacity: 0;
	}

	.mobile-nav-container.active {
		top: 0%;
		opacity: 1;
	}

	.mobile-nav-container nav {
		display: -webkit-flex;
		display: flex;
		width: 100%;
		height: 100%;
		-webkit-flex-direction: column;
		        flex-direction: column;
		-webkit-justify-content: center;
		        justify-content: center;
		-webkit-align-items: center;
		        align-items: center;
	}

	.mobile-nav-container a {
		text-decoration: none;
		margin: 30px;
		color: #000000;
		text-decoration: none;
		font: normal normal bold 40px/1.4em poppins-semibold, poppins, sans-serif;
	}

	.mobile-nav-container a.active {
		color: #0050ff;
	}

	.mobile-nav {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		        flex-direction: column;
		-webkit-justify-content: space-between;
		        justify-content: space-between;
		width: 40px;
		height: 40px;
		transition: -webkit-transform 0.33s ease-out;
		transition: transform 0.33s ease-out;
		transition: transform 0.33s ease-out, -webkit-transform 0.33s ease-out;
		cursor: pointer;
		margin-right: 20px;
	}

	.line {
		width: 100%;
		height: 5px;
		background-color: #0050ff;
		border-radius: 1.5px;
		transition: -webkit-transform 0.33s cubic-bezier(0.54, -0.81, 0.57, 0.57);
		transition: transform 0.33s cubic-bezier(0.54, -0.81, 0.57, 0.57);
		transition: transform 0.33s cubic-bezier(0.54, -0.81, 0.57, 0.57), -webkit-transform 0.33s cubic-bezier(0.54, -0.81, 0.57, 0.57);
	}

	.line.up {
		width: 50%;
		-webkit-transform-origin: right;
		        transform-origin: right;
	}

	.mobile-nav.active .line {
		background-color: #000000;
	}

	.mobile-nav.active .line.up {
		-webkit-transform: rotate(50deg) translateX(12px) translateY(12px);
		        transform: rotate(50deg) translateX(12px) translateY(12px);
	}

	.mobile-nav.active .line.medium {
		-webkit-transform: rotate(-50deg);
		        transform: rotate(-50deg);
	}

	.mobile-nav.active .line.down {
		-webkit-transform: rotate(50deg) translateX(-12px) translateY(-11px);
		        transform: rotate(50deg) translateX(-12px) translateY(-11px);
	}

	.line.down {
		width: 50%;
		-webkit-transform-origin: left;
		        transform-origin: left;
		-webkit-align-self: flex-end;
		        align-self: flex-end;
	}
}

